import uniNavBar from "../../components/uni-nav-bar/uni-nav-bar.vue"
import uploudImages from "../../components/common/uploud-images.vue"
import uniPopup from '../../components/uni-popup/uni-popup.vue'
const itemList = ['所有人可见', '仅自己可见']
export default {
	components: {
		uniNavBar,
		uploudImages,
		uniPopup
	},
	data() {
		return {
			isGet: false,
			visible: '所有人可见',
			textarea: '',
			imgList: [],
			showPopup: true
		}
	},
	onBackPress () {
		// 判断是否有值
		if (!this.textarea && this.imgList.length < 1) return
		if (!this.isGet) {
			this.save()
			return true
		}
	},
	methods: {
		// 保存为草稿
		save () {
			uni.showModal({
				content: '是否要保存为草稿',
				cancelText: '不保存',
				confirmText: '保存',
				success: res => {
					if (res.confirm) {
						console.log('保存')
					} else {
						console.log('不保存')
					}
					this.isGet = true
					this.back()
				}
			})
		},
		// 返回
		back () {
			uni.navigateBack({
				delta: 1
			})
		},
		// 发布
		submit () {
			console.log('发布')
		},
		// 隐私
		changeLook () {
			uni.showActionSheet({
			    itemList: itemList,
			    success: (res) => {
						this.visible = itemList[res.tapIndex]
			    }
			})
		},
		uploud (list) {
			this.imgList = list
			console.log(list)
		},
		hidePopup () {
			this.showPopup = false
		}
	}
}
